import { NgModule, Component } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DashComponent } from './dash/dash.component';
import { BioDataComponent } from './bio-data/bio-data.component';
import { AboutComponent } from './about/about.component';
import { ContactComponent } from './contact/contact.component';
import { ResumeComponent } from './resume/resume.component';
import { PnfComponent } from './pnf/pnf.component';
import { ExperienceComponent } from './experience/experience.component';
import { ServiceComponent } from './service/service.component';
import { P1Component } from './project/p1/p1.component';
import { P2Component } from './project/p2/p2.component';
import { MyPerDetComponent } from './my-details/my-bio-data/my-per-det/my-per-det.component';
import { MyEduDetComponent } from './my-details/my-bio-data/my-edu-det/my-edu-det.component';
import { MyDetailsComponent } from './my-details/my-details.component';
import { EduComponent } from './bio-data/edu/edu.component';
import { SkillComponent } from './bio-data/skill/skill.component';



const routes: Routes = [
  {
      path:'dash',
      component:DashComponent
  },
  {
    path:'service',
    component:ServiceComponent
},

{
 
  path:'bio-data',
  component:BioDataComponent},
  // children:[
    {
      path:"edu",
      component:EduComponent
    },
    {
      path:"exp",
      component:ExperienceComponent
    },
    {
      path:"skill",
      component:SkillComponent
    },



    {
      path:'experience',
      component:ExperienceComponent
    },
    {
      path:'project',
    
      children:[
{
        path:'project1',
        component:P1Component
     },
    {
      path:'project2',
      component:P2Component
    }         
  ]},

    
 
  
{
  path:'about',
  component:AboutComponent
},
{
  path:'contact',
  component:ContactComponent
},
{
  path:'resume',
  component:ResumeComponent
},

  { path: '',
    redirectTo: '/dash',
    pathMatch: 'full'
  },
 
  { path: '**', component:PnfComponent}
];



@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
